  display: none;
}

/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Lato", sans-serif;
  font-size: 1.6rem;
  line-height: 1.8;
  background-image: linear-gradient(to top, #e1e1e1 0%, #FFFFFF 100%);
}

p {
  color: #000000;
  font-weight: 100;
  text-align: center;
}

.contenedor {
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img,
picture {
  width: 100%;
  display: block;
}

h1, h2, h3 {
  font-family: "Lato", sans-serif;
  margin: 2.5rem 0;
  font-weight: 300;
  text-align: center;
}

h1 {
  font-size: 4.8rem;
  font-weight: 700;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

.servicios {
  margin-top: 2.5rem;
  margin-bottom: 2.5rem;
  display: grid;
  display: grid;
  gap: 1rem;
  grid-template-columns: repeat(2, 1fr);
}
@media (min-width: 768px) {
  .servicios {
    display: block;
  }
}

.contenido-centrado {
  max-width: 800px;
}

.sombra {
  -webkit-box-shadow: 0px 5px 15px 0px rgba(112, 112, 112, 0.79);
  -moz-box-shadow: 0px 5px 15px 0px rgba(112, 112, 112, 0.79);
  box-shadow: 0px 5px 15px 0px rgba(112, 112, 112, 0.79);
  background-color: #FFFFFF;
  padding: 2rem;
  border-radius: 1rem;
  margin-top: 2.5rem;
  margin-bottom: 2.5rem;
}

.alinear-derecha {
  display: flex;
  justify-content: flex-end;
}

.boton {
  background-color: #E08709;
  color: #FFFFFF;
  padding: 1rem 3rem;
  margin-top: 2rem;
  text-transform: uppercase;
  font-weight: 700;
  border-radius: 0.5rem;
  text-align: center;
  width: 90%;
}
@media (min-width: 768px) {
  .boton {
    width: auto;
  }
}

.boton-verde {
  background-color: #71B100;
  color: #FFFFFF;
  font-weight: bold;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: block;
}
.boton-verde:hover {
  background-color: #619800;
}

.boton-lila {
  background-color: #9D0CEA;
  color: #FFFFFF;
  font-weight: bold;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
}
.boton-lila:hover {
  background-color: #8d0bd2;
}

.boton-amarillo {
  background-color: #E08709;
  color: #FFFFFF;
  font-weight: bold;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
}
.boton-amarillo:hover {
  background-color: #c77808;
}

boton-rojo {
  background-color: rgb(209, 51, 51);
  color: #FFFFFF;
  font-weight: bold;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
}
boton-rojo:hover {
  background-color: #bf2b2b;
}

.dark-mode {
  background-color: red;
}

.header {
  max-width: 50rem;
  margin: 0 auto;
  background-color: #FFFFFF;
  padding: 1rem 0 3rem 0;
}

.navegacion {
  display: none;
}
@media (min-width: 768px) {
  .navegacion {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
  }
}
.navegacion a {
  width: 100%;
  display: block;
  text-align: center;
  color: #FFFFFF;
  font-size: 2rem;
  font-weight: 700;
  padding: 1rem;
}
.navegacion a:hover {
  background-color: #E08709;
  color: #000000;
}

.nav-bg {
  background-color: #00A0C6;
}

.mobile-menu {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .mobile-menu {
    display: none;
  }
}
.mobile-menu img {
  width: 5rem;
}

.mostrar {
  display: block;
}

.derecha {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .derecha {
    align-items: flex-end;
  }
}

.dark-mode-boton {
  width: 3rem;
}
.dark-mode-boton:hover {
  cursor: pointer;
}

.hero {
  background-image: url(../../../build/img/imagen.png);
  background-repeat: no-repeat;
  background-size: cover;
  height: 450px;
  position: relative;
}

.contenido-hero {
  position: absolute;
  background-color: rgba(0, 0, 0, 0.6);
  color: #FFFFFF;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.contenido-hero p {
  color: #FFFFFF;
  margin-bottom: 0;
}

.ubicacion {
  display: flex;
  align-items: flex-end;
}
.ubicacion img {
  padding-right: 1.5rem;
}

.areas {
  font-weight: 700;
}

@media (min-width: 768px) {
  .contenedor-anuncios {
    display: grid;
    gap: 1rem;
    grid-template-columns: repeat(3, 1fr);
  }
}
.anuncio {
  background-color: #f5950d;
  border: 1px solid #c8c8c8;
  color: #FFFFFF;
}
.anuncio .contenido-anuncio {
  padding: 2rem;
}
.anuncio h3, .anuncio p {
  margin: 0;
}
.anuncio .precio {
  color: #71B100;
  font-weight: 700;
  font-size: 3rem;
}

.no-webp .imagen-contacto {
  background-image: url("../../../build/img/hero.jpg");
}

.webp .imagen-contacto {
  background-image: url("../../../build/img/hero.webp");
}

.imagen-contacto {
  background-position: center center;
  background-size: cover;
  padding: 10rem 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 2rem;
}
.imagen-contacto h2 {
  font-size: 4rem;
  font-weight: 900;
  color: #FFFFFF;
}
.imagen-contacto p {
  font-size: 1.8rem;
  color: #FFFFFF;
}

@media (min-width: 768px) {
  .seccion-inferior {
    display: grid;
    grid-template-columns: 2fr 1fr;
    column-gap: 2rem;
  }
}
.entrada-blog {
  margin-bottom: 2rem;
}
@media (min-width: 768px) {
  .entrada-blog {
    display: grid;
    grid-template-columns: 1fr 2fr;
    column-gap: 2rem;
  }
}
.entrada-blog:last-of-type {
  margin-bottom: 0;
}

.texto-entrada a {
  color: #000000;
  margin-top: 2rem;
  display: block;
}
@media (min-width: 768px) {
  .texto-entrada a {
    margin-top: 0;
  }
}
.texto-entrada h4 {
  margin: 0;
  line-height: 1.4;
}
.texto-entrada h4::after {
  content: "";
  display: block;
  width: 15rem;
  height: 0.5rem;
  background-color: #71B100;
}
.testimonial {
  background-color: #71B100;
  font-size: 2.4rem;
  padding: 2rem;
  color: #FFFFFF;
  border-radius: 2rem;
}
.testimonial blockquote {
  padding-left: 5rem;
}
.testimonial p {
  text-align: right;
  color: #FFFFFF;
}

.formulario p {
  font-size: 1.4rem;
  color: #333333;
  margin: 2rem 0 0 0;
}
.formulario legend {
  font-size: 2rem;
  color: #333333;
}
.formulario label {
  font-weight: 700;
  text-transform: uppercase;
  display: block;
}
.formulario input:not([type=submit]),
.formulario textarea,
.formulario select {
  padding: 1rem;
  display: block;
  width: 100%;
  background-color: #FFFFFF;
  border: 1px solid #e1e1e1;
  border-radius: 1rem;
  margin-bottom: 2rem;
}
.formulario textarea {
  height: 20rem;
}
.formulario .forma-contacto {
  max-width: 30rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.formulario input[type=radio] {
  margin: 0;
  width: auto;
}

.alerta {
  padding: 0.5rem;
  text-align: center;
  color: #FFFFFF;
  font-weight: 700;
  text-transform: uppercase;
  margin: 1rem 0;
}
.alerta.error {
  background-color: rgb(209, 51, 51);
}
.alerta.exito {
  background-color: #71B100;
}

table.productos {
  margin-top: 4rem;
  width: 100%;
  border-spacing: 0;
}
table.productos thead {
  background-color: #71B100;
}
table.productos thead th {
  color: #FFFFFF;
  padding: 2rem;
}
table.productos .imagen-tabla {
  width: 10rem;
}